            {
              id: 5768,
              name: '萧敬腾',
              avatar:
                'http://p4.music.126.net/kIyvVOBtz0sPJMom1ss6tA==/1382086116668101.jpg'
            }
          ]
        },
        {
          title: 'Y',
          items: [
            {
              id: 6066,
              name: '杨宗纬',
              avatar:
                'http://p3.music.126.net/Q4JSaV98wuU6xElATsFjAw==/3261151495434543.jpg'
            },
            {
              id: 10204,
              name: '杨千嬅',
              avatar:
                'http://p3.music.126.net/Fy3OSgevuA-iwsy3X_pqpA==/235295488361002.jpg'
            }
          ]
        },
        {
          title: 'Z',
          items: [
            {
              id: 6452,
              name: '周杰伦',
              avatar:
                'http://p3.music.126.net/o-FjCrUlhyFC96xiVvJZ8g==/109951163111191410.jpg'
            },
            {
              id: 6460,
              name: '张学友',
              avatar:
                'http://p3.music.126.net/1JG8Uwzb5XAJXaPUHR6RBw==/3238061747024716.jpg'
            },
            {
              id: 6731,
              name: '赵雷',
              avatar:
                'http://p4.music.126.net/w_UWOls2uCkFN_U62788Xg==/18806046882229308.jpg'
            },
            {
              id: 6457,
              name: '张国荣',
              avatar:
                'http://p4.music.126.net/wupRd6PIODPM2gMxmlxD8w==/39582418617683.jpg'
            },
            {
              id: 6462,
              name: '张敬轩',
              avatar:
                'http://p3.music.126.net/7a77DZlspSaBZexOr8mdLw==/18863221486279082.jpg'
            },
            {
              id: 10561,
              name: '张靓颖',
              avatar:
                'http://p4.music.126.net/PDSLSEj5EHvGWeaTy5MqWQ==/109951163064519620.jpg'
            },
            {
              id: 10559,
              name: '张惠妹',
              avatar:
                'http://p4.music.126.net/F9asgcj7C7qSl_je9XDvRw==/603631883675241.jpg'
            },
            {
              id: 6472,
              name: '张杰',
              avatar:
                'http://p4.music.126.net/R4it3K4VVd4qOlMv1VvH_w==/18569651881628300.jpg'
            },
            {
              id: 6652,
              name: '周传雄',
              avatar:
                'http://p3.music.126.net/UXycaJG3B1yhBMidEjqH7w==/211106232550480.jpg'
            },
            {
              id: 6454,
              name: '张信哲',
              avatar:
                'http://p3.music.126.net/dtm0L5L0OtxDzDpnFTqVyg==/3285340750049091.jpg'
            }
          ]
        }
      ],
      scrollY: 0,
      currentIndex: 0
    }
  },
  created () {
    this.touch = {}
    // 初始化 better-scroll 必须要等 dom 加载完毕
    setTimeout(() => {
      this._initSrcoll()
      this._calculateHeight()
    }, 20)
  },
  methods: {
    _initSrcoll () {
      // console.log('didi')
      this.scroll = new BScroll(this.$refs.listView, {
        probeType: 3,
        click: true
      })

      this.scroll.on('scroll', (pos) => {
        this.scrollY = pos.y
      })
    },
    onShortcutStart (e) {
      // 获取到绑定的 index
      let index = e.target.getAttribute('data-index')
      // 使用 better-scroll 的 scrollToElement 方法实现跳转
      this.scrollToElement(index)

      // 记录一下点击时候的 Y坐标 和 index
      let firstTouch = e.touches[0].pageY
      this.touch.y1 = firstTouch
      this.touch.anchorIndex = index
    },
    onShortcutMove (e) {
      // 再记录一下移动时候的 Y坐标，然后计算出移动了几个索引
      let touchMove = e.touches[0].pageY
      this.touch.y2 = touchMove
      // 这里的 16.7 是索引元素的高度
      let delta = Math.floor((this.touch.y2 - this.touch.y1) / 16.7)

      // 计算最后的位置
      // * 1 是因为 this.touch.anchorIndex 是字符串，用 * 1 偷懒的转化一下
      let index = this.touch.anchorIndex * 1 + delta
      this.scrollToElement(index)
    },
    scrollToElement (index) {
      // 处理边界情况
      // 因为 index 通过滑动距离计算出来的
      // 所以向上滑超过索引框框的时候就会 < 0，向上就会超过最大值
      if (index < 0) {
        return
      } else if (index > this.listHeight.length - 2) {
        index = this.listHeight.length - 2
      }
      // listHeight 是正的， 所以加个 -
      this.scrollY = -this.listHeight[index]
      this.scroll.scrollToElement(this.$refs.listGroup[index])
    },
    _calculateHeight () {
      this.listHeight = []
      const list = this.$refs.listGroup
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < list.length; i++) {
        let item = list[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
    }
  },
  watch: {
    scrollY (newVal) {
      // 向下滑动的时候 newVal 是一个负数，所以当 newVal > 0 时，currentIndex 直接为 0
      if (newVal > 0) {
        this.currentIndex = 0
        return
      }
      
      // 计算 currentIndex 的值
      for (let i = 0; i < this.listHeight.length - 1; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]

        if (-newVal >= height1 && -newVal < height2) {
          this.currentIndex = i
          return
        }
      }
      
      // 当超 -newVal > 最后一个高度的时候
      // 因为 this.listHeight 有头尾，所以需要 - 2
      this.currentIndex = this.listHeight.length - 2
    }
  },
  computed: {
    shortcutList () {
      return this.singers.map((group) => {
        return group.title.substr(0, 1)
      })
    }
  }
}
</script>

<style lang="scss">
.box {
  position: fixed;
  width: 100%;
  height: 100%;
}
.list-view {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: rgb(223, 223, 223);
  .list-group {
    padding-bottom: 30px;
    .list-group-title {
      height: 30px;
      line-height: 30px;
      padding-left: 20px;
      font-size: 12px;
      color: #FFF;
      background: #C20C0C;
    }
    .list-group-item {
      display: flex;
      align-items: center;
      padding: 20px 0 0 30px;
      .avatar {
        width: 50px;
        height: 50px;
        border-radius: 5%;
      }
      .name {
        margin-left: 20px;
        color: black;
        font-size: 14px;
        // font-weight: 500;
      }
    }
  }
  .list-shortcut {
    position: absolute;
    z-index: 30;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    padding: 20px 0;
    border-radius: 10px;
    text-align: center;
    background: rgba(167, 167, 167, 0.5);
    font-family:Helvetica;
    .item {
      padding: 3px;
      line-height: 1;
      color: black;
      font-size: 11px;
      &.current {
        color: #C20C0C;
        // font-weight: bold;
      }
    }
  }
}
</style>



